<template>
    <div class="about d-flex">
        <div class="d-flex flex-column justify-content-center"> 
            <span v-if="page > 1" class="d-flex carousel-control-prev-icon" aria-hidden="true" @click="prevPage"></span>
        </div>
        <transition name="flip" mode="out-in">
        <div class="about-page d-flex flex-column  justify-content-center" v-if="page == 1">
            <p>Жанр: MMORPG-sandbox</p>
            <p>Графика: 2D в изометрической проекции</p>
            <p>Боевая система: карточные бои, где каждая карта – сам игрок со своими собственными способностями</p>
            <p>Режим игры: многопользовательский</p>
            <p>Платформы: Android, iOS.</p>
        </div>
        <div class="about-page d-flex flex-column  justify-content-center" v-else-if="page == 2">
            <p>
                Вам когда-либо хотелось заглянуть за завесу времени и пространства? Оказаться в доисторическом прошлом и продемонстрировать силу и ловкость в охоте на мамонта? Проявить смекалку в борьбе за огонь? А может вы с детства грезили рыцарскими турнирами?
            </p>
        </div>
        <div class="about-page d-flex flex-column  justify-content-center" v-else-if="page == 3">
            <p>
                Или вовсе мечтали выпить чашечку кофе на борту личного космолёта, 
                наблюдая за рождением новой звезды? А теперь представьте, что это стало реальностью!
            </p>
        </div>
        <div class="about-page d-flex flex-column  justify-content-center" v-else-if="page == 4">
            <p>
                Dreamer-Online – MMORPG в реальном времени, разрабатываемая командой Kvarq на платформах Android и iOS, 
                даст вам такую возможность. 
                Совершите своё уникальное путешествие через семь исторических периодов-эпох от каменного века, 
                средневековьz и вплоть до новейшего времени и будущего. Но и это не предел, 
                ведь самое интересное только начинается!
            </p>
        </div>
        <div class="about-page d-flex flex-column  justify-content-center" v-else-if="page == 5">
            <p>
                Имея на начальном этапе лишь примитивные знания и умения, развивайтесь, добывайте ресурсы, занимайтесь производством, 
                стройте свои здания и образовывайте поселения, объединяйтесь в сообщества для обмена и взаимопомощи. 
                Ну и конечно, с головой погружайтесь в мир карточных баталий! И для этого вам совсем не потребуется колода, 
                ведь на поле боя вы – это отдельная карта со своими индивидуальными характеристиками,
                а союзниками и соперниками вам выступят реальные игроки. 
                Бросьте вызов любителям карточных битв из других городов, стран и даже эпох. 
            </p>
        </div>
        <div class="about-page d-flex flex-column  justify-content-center" v-if="page == 6">
            <p>
                Сражайтесь, одерживайте победы, и вы станете лидером!
                С постепенным изучением игрового мира перед вами откроется весь его потенциал. Но, как и наша родная Земля, он полон опасностей и уязвимостей, которые могут привести к гибели этого прекрасного и удивительного места.
                Казалось бы, всё так просто и знакомо. Однако только от вашего решения зависит, каким станет этот мир.
                Творите свою историю сами!
                И помните – здесь всё реально!
            </p>
        </div>
        </transition>
        <div v-if="page < 4" class="d-flex flex-column justify-content-center" @click="nextPage"> 
            <span class="d-flex carousel-control-next-icon" aria-hidden="true"></span>
        </div>
    </div>
    
</template>



<script>
export default {
    data() {
        return {
            page: 1
        }
    },
    methods: {
        nextPage() {
            if (this.page < 4) {
                this.page++;
            }

        },
        prevPage() {
            if (this.page > 1) {
                this.page--;
            }
        }
    }
}
</script>


<style scoped>

</style>




