<template>
    <div> 
        <div class="row forum-btn-wrapper d-flex justify-content-between">
            <router-link to="/forum" v-if="!postOpened && !createNew" class="forum-btn">
                <button type="button" class="btn btn-outline-secondary">Назад</button>
            </router-link>
            
            <button type="button" class="close " aria-label="Close" v-else @click="closePost">
                <span class="d-inline-flex" aria-hidden="true">&times;</span>
            </button>
            <button type="button" class="btn btn-outline-secondary" v-if="(!postOpened && !createNew && this.signComplete)" @click="addPost">Новый пост</button>
                <span class="sign-in-or-sigh-up" v-else-if="!postOpened && !createNew">Чтобы добавлять посты, пожалуйста авторизуйтесь или зарегистрируйтесь</span>
            <button type="submit" class="btn btn-primary onLoaded hidden" @click="loadedData">loadedData</button>
        </div>
        <ul v-if="createNew === false && postOpened === false"  class="posts-list">
            <li v-for="post in posts" v-bind:key="post.id" @click="openPost(post.id)" class="bg-secondary">
                <div class="container-fluid postList">
                    <div class="row">
                        <div class="col">
                            <h4>
                                {{ post.title }}
                            </h4>
                        </div>
                        <div class="col">
                            <p>
                                {{ post.author }}
                            </p>
                        </div>
                        <div class="col">
                            <p>
                                {{ post.date.date }}
                            </p>
                            <p>
                                {{ post.date.time }}
                            </p>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
        <div v-if="postOpened" class="container-fluid openedPost">
            <h3>{{post.title}}</h3>
            <div class="row post bg-secondary">
                <div class="col-sm-3 authorPost">
                    <h5>{{post.author}}</h5>
                    <span>{{post.date.date}}</span>
                    <hr>
                    <span>{{post.date.time}}</span>
                </div>
                <div class="col bodyPost">
                    {{post.body}}
                </div>
            </div>
            <div class="comments-wrapper bg-light">
                <ul class="comments">
                    <li class="row" v-for="comment in post.comments" v-bind:key="comment.id">
                        <div class="col-sm-3 comment-author">
                            <span>{{comment.author}}</span>
                            <hr>
                            <span>{{comment.date.date}}</span>
                            <hr>
                            <span>{{comment.date.time}}</span>
                        </div>
                        <div class="col comment-text">
                            <p>
                                {{comment.text}}
                            </p>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <div v-if="postOpened && this.signComplete" class="container-fluid newComment">
            <form class="mt-5" @submit.prevent="addComment">
                <div class="form-group">
                    <textarea class="form-control" rows="5" v-model="newCommentText"></textarea>
                    <button type="submit" class="btn btn-outline-secondary">Отправить</button>
                </div>
            </form>
        </div>
        <span v-if="postOpened && !this.signComplete">
            Войдите или зарегистрируйтесь, чтобы оставлять комментарии.
        </span>
        <div v-if="createNew" class="container-fluid newPost">
            <form class="mt-5" @submit.prevent="sendNewPost">
                <div class="form-group">
                    <input class="form-control" type="text" placeholder="введите заголовок поста" v-model="newPostTitle">
                    <textarea class="form-control" rows="10" v-model="newPostBody"></textarea>
                    <button type="submit" class="btn btn-outline-secondary">Отправить</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>

export default {
    props: ['signComplete'],
    name: 'app',
    data () {
        return {
            posts: [],
            post: {},
            createNew: false,
            newPostBody: '',
            newPostTitle: '',
            postOpened: false,
            newCommentText: '',
        }
    },
    mounted() {
        this.loadedData();
    },
    methods: {
        loadedData() {
            let starCountRef = firebase.database().ref('forum/' + this.$route.params.Tid + '/');
            starCountRef.on('value', (snapshot) => {
                let obj = snapshot.val();
                    this.dataFromBase(obj);
                            // длинна Object.keys(obj).length
                            // значение obj[Object.keys(obj)[0]]
                            // имя ключа Object.keys(obj)[0]
            }); 
        },
        dataFromBase(obj) {
            let posts = [];
            for (let i = 0; i < Object.keys(obj).length; i++) {
                obj[Object.keys(obj)[i]].id = Object.keys(obj)[i];
                let post = obj[Object.keys(obj)[i]];
                posts.push(post);
            }
            this.posts = posts;

        },
        addPost() {
            this.postOpened = false;
            this.createNew = !this.createNew;
        },
        getCurrentDate() {
            let date = new Date();
            let currentDate = {};
            currentDate.date = `${date.getFullYear()}/${date.getMonth()+1}/${date.getDate()}`;
            currentDate.time = `${date.getHours()}:${date.getMinutes()}`;
            return currentDate
        },
        sendNewPost() {
            if ((this.newPostBody === '') || (this.newPostTitle === '')) {
                console.log('Заполните необходимые поля');
            }
            else {
              // A post entry.
            var postData = {
                author: localStorage.email,
                uid: localStorage.uid,
                body: this.newPostBody,
                title: this.newPostTitle,
                comments: [],
                date: this.getCurrentDate()
            };
            // Get a key for a new Post.
            var newPostKey = firebase.database().ref().child('/'+ this.$route.params.Tid).push().key;

            // Write the new post's data simultaneously in the posts list and the user's post list.
            var updates = {};
                updates['/forum/' + this.$route.params.Tid + '/' + newPostKey] = postData;
             //   updates['/user-posts/' + uid + '/' + newPostKey] = postData;
            this.createNew = false;
            this.newPostBody = '';
            this.newPostTitle = '';
            return firebase.database().ref().update(updates);   
            }
        },
        openPost(id) {
            for (let i = 0; i < this.posts.length; i++) {
                if (this.posts[i].id === id) {
                    this.post = this.posts[i];
                    this.postOpened = true;
                }
            }
        },
        closePost() {
            this.postOpened = false;
            this.createNew = false;
            this.newPostBody = '';
            this.newPostTitle = '';
            this.newCommentText = '';
        },
        addComment() {
            if (this.post.comments === undefined) {
                this.post.comments = [];
            }
            if (this.newCommentText === '') {
                console.log('Введите комментарий');
            }
            else {
                let comment = {};
                comment.date = this.getCurrentDate();
                comment.author = localStorage.email;
                comment.text = this.newCommentText;
              //  console.log(comment.author, comment.date.date, comment.date.time, comment.text)
              //  this.post.comments.push(this.newCommentText);
                this.post.comments.push(comment);
                console.log(this.post.id)
                let updates = {};
                    updates['/forum/' + this.$route.params.Tid +'/' + this.post.id + '/comments'] = this.post.comments;
                this.newCommentText = '';
                return firebase.database().ref().update(updates); 
            }

        }
    }
}
</script>

<style>

</style>


